@import url('https://fonts.googleapis.com/css2?family=Baloo+2&display=swap');
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
   font-family: 'Baloo 2', cursive;
}
.main_div{
    width: 100%;
    height: 100vh;
    background: #7bed9f;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.center_div{
    width: 25%;
    height: 80vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px 5px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
}

h1{
    color: rgba(0, 0, 0, 0.87);
    font-size: 50px;
    margin-bottom: 30px;
    min-width: 64px;
    box-sizing: border-box;
    font-weight: 500;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    
    text-transform: uppercase;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}
input{
    text-align: center;
    height: 40px;
    top: 10px;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    width: 60%;
    border-bottom: 5px solid #8566aa;
    outline: none;

}
button{
    min-height: 40px;
    width: 45px;
    border-radius: 50%;
    border-color: transparent;
    background-color: #a085c0;
    color: #fff;
    font-size: 40px;
    border: none;
    outline: none;
    margin-left: 10px;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
button:hover{
background-color: #20bf6b;
}
ol{
    margin-top: 30px;
  
}

ol li{
    padding-left: 0px;
    text-align: left;
    font-size: 20px;
    min-height: 40px;
    font-weight: 500;
    display: flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;
}
.newBtn {
 width: 20px;
 height: 50px;
 background-color: #16a085 !important;
color: white !important;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    border-radius: 50% !important;
}

.newBtn:hover {
color: #16a085 !important;
background: white !important;
 }
  
 .deleteIcon {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  padding: 5px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  
  
  
  }
  
 .deleteIcon:hover {



    background-color: transparent;
  
  
  
    color: crimson;
  
  
  
    box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);
  
  
  
  }
  
  
  
  
  
  
.todo_style{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.todo_style .fa-times{
 width: 20px;
 height: 20px;
 display: flex;
 justify-content: center;
 align-items: center;
 background-color: #8566aa;
 border-radius: 50%;
 margin:0px 15px 0 35px;
 color: aliceblue;
 box-shadow: 5px 5px 15px -5px rgba(0, 0,0, 0.3);
}

.todo_style .fa-times:hover{
    background-color: crimson;
}
.todo_style .fa-times:hover .list_style{
    color: crimson;
}

@media(max-width: 768px){
    .center_div{
        width: 55%;
    }


.todo_style .fa-times{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;

}}